.App {
  text-align: center;
}
.App-problem-box {
  width: 300px;
}
.App-button-container {
  display: inline-block;
}

.App-button-container .Header-Button-holder button {
  display: block;
}

.App-logo-container {
  display: inline-block;
}
.jack-block.blockborderless {
  border: none;
}
.centertext {
  width: 150px;
  padding-left: 12rem;
}
.embedmargin {
  margin-right: 460px;
}

.jack-paragraph {
  width: 800px;

}

.jacks-big-container { 
  width: 1200px;
  margin: 40px;
}

.jack-block {
  width: 100px;
  border: 2px solid black;
  display: inline-block !important;
  word-wrap: normal;
  padding: 5vh;
}

.jack-container-blocks {
  width: 900px;
}
.jack-totals-span {
  display: block;
}

.income-green {
  border-color: greenyellow;
  color: greenyellow;
}
.App-logo {
  height: 21vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
